<template>
  <div class="BasisInformationTotalBackground">
    <!--     <input
      type="text"
      class="BasisInformationUserNumberInput"
      v-model="BasisInformationUserNumber"
      placeholder="请输入学生学号"
    />
    <button  class="BasisInformationSeekButton" @click="BasisInformationSeekButtonClick">查询</button>
    <button  class="BasisInformationEditButton" @click="BasisInformationEditButtonClick">编辑</button>
 -->

    <div class="BasisInformationTableBackground">
      <table>
        <thead>
          <tr>
            <th v-if="ifEdit" style="width: 1%">-</th>
            <th style="width: 2%">学号</th>
            <th style="width: 2%">姓名</th>
            <th style="width: 6%">家庭住址</th>
            <th style="width: 2%">学生个人手机号</th>
            <th style="width: 2%">监护人关系</th>
            <th style="width: 2%">监护人对应手机号</th>
            <th style="width: 2%">政治面貌</th>
            <th style="width: 1%">班级</th>
            <th style="width: 3%">职务</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="records in StudentBasisInformationTable"
            :key="records.studentNumber"
          >
            <!-- 如果在tr标签里写点击事件就是一行  @click="handleRowClick(records.id)" -->
            <td
              v-if="ifEdit"
              class="EditIcon"
              @click="DeleteLineData(records.studentNumber)"
            >
              <el-icon><Remove /></el-icon>
            </td>
            <td>{{ records.studentNumber }}</td>
            <td>{{ records.studentName }}</td>
            <td>{{ records.familyPlace }}</td>
            <td>{{ records.studentPhoneNumber1 }}</td>
            <td>{{ records.guardianShip1 }}</td>
            <td>{{ records.guardianPhoneNumber1 }}</td>
            <td>{{ records.politicalStatus }}</td>
            <td>{{ records.classes }}</td>
            <td>{{ records.job1 }}</td>
            <td @click="ViewDetails(records)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="ControlBasisInformationTableButtonBackground">
      <button class="EditButton" v-if="!ifEdit" @click="OpenEditButtonClick">
        编辑<span v-if="ifEdit" @click="CloseEditButtonClick">编辑完成</span>
      </button>
      <button class="EditButton" v-if="ifEdit" @click="CloseEditButtonClick">
        编辑完成
      </button>
      <button class="AddDataButton" v-if="ifEdit" @click="AddDataButtonClick">
        <el-icon><Plus /></el-icon>
      </button>
    </div>

    <div class="StudentBasisPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>
    <div v-if="ifDetails" class="BasisInformationDetailsBackground">
      <div class="BasisInformationDetailsFormBackground">
        <span class="BasisInformationDetailsFormTitle">学生详细信息</span>
        <form class="BasisInformationDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails.name }}</label>
            <label>籍贯 : {{ studentDetails.nativePlace }}</label>
            <label>性别 : {{ studentDetails.gender }}</label>
          </div>
          <div class="label-row">
            <label>健康状态 : {{ studentDetails.healthStatus }}</label>
            <label>政治面貌 : {{ studentDetails.politicalStatus }}</label>
          </div>

          <div class="label-row">
            <label>出生日期 : {{ studentDetails.birthday }}</label>
          </div>
          <div class="label-row">
            <label>身份证号 : {{ studentDetails.idCard }}</label>
          </div>
          <div class="label-row">
            <label>家庭住址 : {{ studentDetails.familyPlace }}</label>
          </div>
          <div class="label-row">
            <label>身高(cm) : {{ studentDetails.height }}</label>
            <label>特长 : {{ studentDetails.specialty }}</label>
            <label>爱好 : {{ studentDetails.hobby }}</label>
          </div>
          <div class="label-row">
            <label
              >学生个人手机号1 : {{ studentDetails.studentPhoneNumber1 }}</label
            >
          </div>
          <div class="label-row">
            <label
              >学生个人手机号2 : {{ studentDetails.studentPhoneNumber2 }}</label
            >
          </div>
          <div class="label-row">
            <label>监护人关系1 : {{ studentDetails.guardianShip1 }}</label>
            <label
              >监护人对应手机号1 :
              {{ studentDetails.guardianPhoneNumber1 }}</label
            >
          </div>
          <div class="label-row">
            <label>监护人关系2 : {{ studentDetails.guardianShip2 }}</label>
            <label
              >监护人对应手机号2 :
              {{ studentDetails.guardianPhoneNumber2 }}</label
            >
          </div>
          <div class="label-row">
            <label>QQ号 : {{ studentDetails.qqNumber }}</label>
          </div>

          <div class="label-row">
            <label>微信号 : {{ studentDetails.wechatNumber }}</label>
          </div>
          <div class="label-row">
            <label>邮箱号码 : {{ studentDetails.email }}</label>
          </div>
          <br />

          <div class="label-row">
            <label>学号 : {{ studentDetails.studentNumber }}</label>
            <label>寝室号 : {{ studentDetails.dormitoryNumber }}</label>
          </div>
          <div class="label-row">
            <label>学院 : {{ studentDetails.college }}</label>
            <label>专业 : {{ studentDetails.major }}</label>
          </div>

          <div class="label-row">
            <label>年级 : {{ studentDetails.grade }}</label>
            <label>班级 : {{ studentDetails.classes }}</label>
          </div>
          <div class="label-row">
            <label>职务1 : {{ studentDetails.job1 }}</label>
            <label>职务2 : {{ studentDetails.job2 }}</label>
            <label>职务3 : {{ studentDetails.job3 }}</label>
          </div>
        </form>
        <button
          class="BasisInformationDetailsFormCloseButton"
          @click="BasisInformationDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>

    <div v-if="ifAddTable" class="BasisInformationAddTableBackground">
      <div class="BasisInformationAddTableFormBackground">
        <span class="BasisInformationAddTableFormTitle">新增学生信息</span>
        <form class="BasisInformationAddTableForm">
          <div class="AddTable-label-row">
            <label>姓名 : </label>
            <input v-model="studentName" type="text" placeholder="请输入姓名" />
          </div>
          <div class="AddTable-label-row">
            <label>籍贯 : </label>
            <input v-model="nativePlace" type="text" placeholder="请输入籍贯" />
          </div>
          <div class="AddTable-label-row">
            <label>性别 : </label>
            <el-radio-group v-model="sex">
              <el-radio value="男">男</el-radio>
              <el-radio value="女">女</el-radio>
            </el-radio-group>
          </div>

          <div class="AddTable-label-row">
            <label>健康状态 :</label>

            <select v-model="healthStatus" id="healthStatus">
              <option disabled value="">请选择健康状态</option>
              <option v-for="item in myHealthStatus" :key="item" :value="item">
                {{ item }}
              </option>
            </select>
          </div>
          <div class="AddTable-label-row">
            <label>政治面貌 :</label>

            <select v-model="politicalStatus" id="politicalStatus">
              <option disabled value="">请选择政治面貌</option>
              <option
                v-for="item in myPoliticalStatus"
                :key="item"
                :value="item"
              >
                {{ item }}
              </option>
            </select>
          </div>

          <div class="AddTable-label-row">
            <label>出生日期 :</label>
            <input v-model="birthday" type="date" />
          </div>

          <div class="AddTable-label-row">
            <label>身份证号 :</label>
            <input v-model="idCard" type="text" placeholder="请输入身份证号" />
          </div>

          <div class="AddTable-label-row">
            <label>家庭住址 : </label>
            <input
              v-model="familyPlace"
              type="text"
              placeholder="请输入家庭住址"
            />
          </div>

          <div class="AddTable-label-row">
            <label>身高(cm) :</label>
            <input v-model="height" type="number" placeholder="请输入身高" />
          </div>
          <div class="AddTable-label-row">
            <label>特长 :</label>
            <input v-model="specialty" type="text" placeholder="请输入特长" />
          </div>
          <div class="AddTable-label-row">
            <label>爱好 :</label>
            <input v-model="hobby" type="text" placeholder="请输入爱好" />
          </div>

          <div class="AddTable-label-row">
            <label>学生个人手机号1 :</label>
            <input
              v-model="studentPhoneNumber1"
              type="text"
              placeholder="请输入手机号1"
            />
          </div>

          <div class="AddTable-label-row">
            <label>学生个人手机号2 :</label>
            <input
              v-model="studentPhoneNumber2"
              type="text"
              placeholder="请输入手机号2"
            />
          </div>

          <div class="AddTable-label-row">
            <label>监护人关系1 : </label>
            <input
              v-model="guardianShip1"
              type="text"
              placeholder="请输入监护人关系1"
            />
          </div>

          <div class="AddTable-label-row">
            <label>监护人对应手机号1 : </label>
            <input
              v-model="guardianPhoneNumber1"
              type="text"
              placeholder="请输入监护人手机号1"
            />
          </div>

          <div class="AddTable-label-row">
            <label>监护人关系2 :</label>
            <input
              v-model="guardianShip2"
              type="text"
              placeholder="请输入监护人关系2"
            />
          </div>
          <div class="AddTable-label-row">
            <label>监护人对应手机号2 : </label>
            <input
              v-model="guardianPhoneNumber2"
              type="text"
              placeholder="请输入监护人手机号2"
            />
          </div>

          <div class="AddTable-label-row">
            <label>QQ号 : </label>
            <input v-model="qqNumber" type="text" placeholder="请输入QQ号" />
          </div>

          <div class="AddTable-label-row">
            <label>微信号 : </label>
            <input
              v-model="wechatNumber"
              type="text"
              placeholder="请输入微信号"
            />
          </div>

          <div class="AddTable-label-row">
            <label>邮箱号码 :</label>
            <input v-model="email" type="email" placeholder="请输入邮箱号码" />
          </div>
          <br />

          <div class="AddTable-label-row">
            <label>学号 : </label>
            <input
              v-model="studentNumber"
              type="text"
              placeholder="请输入学号"
            />
          </div>
          <div class="AddTable-label-row">
            <label>寝室号 : </label>
            <input
              v-model="dormitoryNumber"
              type="text"
              placeholder="请输入寝室号"
            />
          </div>

          <div class="AddTable-label-row">
            <label>学院 : </label>
            <input v-model="college" type="text" placeholder="请输入学院" />
          </div>
          <div class="AddTable-label-row">
            <label>专业 :</label>
            <input v-model="major" type="text" placeholder="请输入专业" />
            <!--  <select v-model="major" id="major">
              <option disabled value="">请选择专业</option>
              <option v-for="item in majors" :key="item" :value="item">
                {{ item }}
              </option>
            </select> -->
          </div>

          <div class="AddTable-label-row">
            <label>年级 :</label>
            <input v-model="grade" type="text" placeholder="请输入年级" />
          </div>
          <div class="AddTable-label-row">
            <label>班级 :</label>
            <input v-model="classes" type="text" placeholder="请输入班级" />
          </div>

          <div class="AddTable-label-row">
            <label>职务1 : </label>
            <input v-model="job1" type="text" placeholder="请输入职务1" />
          </div>
          <div class="AddTable-label-row">
            <label>职务2 :</label>
            <input v-model="job2" type="text" placeholder="请输入职务2" />
          </div>
          <div class="AddTable-label-row">
            <label>职务3 :</label>
            <input v-model="job3" type="text" placeholder="请输入职务3" />
          </div>
        </form>
        <button
          class="BasisInformationAddTableFormCloseButton"
          @click="BasisInformationAddTableFormCloseButtonClick"
        >
          确认
        </button>
        <button
          class="BasisInformationAddTableFormCloseNoButton"
          @click="BasisInformationAddTableFormCloseNoButtonClick"
        >
          取消
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/axios";
import { ElMessage, ElMessageBox } from "element-plus";

export default {
  data() {
    return {
      StudentBasisInformationTable: [],
      ifDetails: false,
      ifEdit: false,
      ifAddTable: false,
      studentDetails: {},
      currentPage: 1,
      pageSize: 12,
      total: 130,
      studentNumber: "",
      studentName: "",
      sex: "",
      birthday: "",
      nativePlace: "",
      height: "",
      idCard: "",
      familyPlace: "",
      studentPhoneNumber1: "",
      studentPhoneNumber2: "无",
      guardianShip1: "",
      guardianPhoneNumber1: "",
      guardianShip2: "无",
      guardianPhoneNumber2: "无",
      qqNumber: "",
      email: "",
      wechatNumber: "",
      specialty: "",
      hobby: "",
      college: "",
      major: "",
      grade: "",
      classes: "",
      job1: "",
      job2: "无",
      job3: "无",
      dormitoryNumber: "",
      healthStatus: "",
      politicalStatus: "",
      myHealthStatus: ["健康", "亚健康", "疾病前驱", "疾病"],
      myPoliticalStatus: [
        "群众",
        "共青团员",
        "中共预备党员",
        "中共党员",
        "民革党员",
        "民盟盟员",
        "民建会员",
        "民进会员",
        "农工党党员",
        "致公党党员",
        "九三学社社员",
        "台盟盟员",
        "无党派人士",
      ],
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    AddDataButtonClick() {
      this.ifAddTable = true;
    },
    OpenEditButtonClick() {
      this.ifEdit = true;
    },
    CloseEditButtonClick() {
      this.ifEdit = false;
    },
    ConfirmationBox(studentNumber) {
      ElMessageBox.confirm("是否删除该学生信息？", "提示", {
        confirmButtonText: "确认",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.DeletePost(studentNumber);
        })
        .catch(() => {
          ElMessage({
            type: "info",
            message: "已取消操作",
          });
        });
    },

    async DeletePost(studentNumber) {
      try {
        const DeleteInformation = await axios.post(
          "/admin/deleteStudentInformation",
          { studentNumber }
        );
        if (DeleteInformation.code == 1) {
          ElMessage({
            type: "success",
            message: "删除成功！",
          });
        } else {
          ElMessage({
            type: "info",
            message: "删除失败！",
          });
        }
      } catch (error) {
        console.error("Axios error:", error);
      }
    },

    DeleteLineData(studentNumber) {
      this.ConfirmationBox(studentNumber);
    },

    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getStudentInformationByPage",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.StudentBasisInformationTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },
    RenewLabel(response) {
      // 更新 studentDetails 中的属性
      this.studentDetails = {
        name: response.studentName,
        grade: response.grade,
        gender: response.sex,
        healthStatus: response.healthStatus,
        politicalStatus: response.politicalStatus,
        birthday: response.birthday,
        idCard: response.idCard,
        familyPlace: response.familyPlace,
        height: response.height,
        specialty: response.specialty,
        hobby: response.hobby,
        studentPhoneNumber1: response.studentPhoneNumber1,
        studentPhoneNumber2: response.studentPhoneNumber2,
        guardianShip1: response.guardianShip1,
        guardianPhoneNumber1: response.guardianPhoneNumber1,
        guardianShip2: response.guardianShip2,
        guardianPhoneNumber2: response.guardianPhoneNumber2,
        qqNumber: response.qqNumber,
        email: response.email,
        wechatNumber: response.wechatNumber,
        college: response.college,
        major: response.major,
        job1: response.job1,
        job2: response.job2,
        job3: response.job3,
        dormitoryNumber: response.dormitoryNumber,
        studentNumber: response.studentNumber,
        nativePlace: response.nativePlace,
        classes: response.classes,
      };
    },
    RenewAddInput() {
      this.studentNumber = "";
      this.studentName = "";
      this.sex = "";
      this.birthday = "";
      this.nativePlace = "";
      this.height = "";
      this.idCard = "";
      this.familyPlace = "";
      this.studentPhoneNumber1 = "";
      this.studentPhoneNumber2 = "无";
      this.guardianShip1 = "";
      this.guardianPhoneNumber1 = "";
      this.guardianShip2 = "无";
      this.guardianPhoneNumber2 = "无";
      this.qqNumber = "";
      this.email = "";
      this.wechatNumber = "";
      this.specialty = "";
      this.hobby = "";
      this.college = "";
      this.major = "";
      this.grade = "";
      this.classes = "";
      this.job1 = "";
      this.job2 = "无";
      this.job3 = "无";
      this.dormitoryNumber = "";
      this.healthStatus = "";
      this.politicalStatus = "";
    },
    async AddLineData() {
      const data = {
        studentNumber: this.studentNumber,
        studentName: this.studentName,
        sex: this.sex,
        birthday: this.birthday,
        nativePlace: this.nativePlace,
        height: this.height,
        idCard: this.idCard,
        familyPlace: this.familyPlace,
        studentPhoneNumber1: this.studentPhoneNumber1,
        studentPhoneNumber2: this.studentPhoneNumber2,
        guardianShip1: this.guardianShip1,
        guardianPhoneNumber1: this.guardianPhoneNumber1,
        guardianShip2: this.guardianShip2,
        guardianPhoneNumber2: this.guardianPhoneNumber2,
        qqNumber: this.qqNumber,
        email: this.email,
        wechatNumber: this.wechatNumber,
        specialty: this.specialty,
        hobby: this.hobby,
        college: this.college,
        major: this.major,
        grade: this.grade,
        classes: this.classes,
        job1: this.job1,
        job2: this.job2,
        job3: this.job3,
        dormitoryNumber: this.dormitoryNumber,
        healthStatus: this.healthStatus,
        politicalStatus: this.politicalStatus,
      };

      const emptyFields = Object.keys(data).filter((key) => {
        return (
          data[key] === null || data[key] === undefined || data[key] === ""
        );
      });

      if (emptyFields.length > 0) {
        this.$message.error(
          `请填写完整信息，以下字段不能为空：<br>${emptyFields.join("<br>")}`
        );
        return; // 如果有空值，终止继续执行
      }

      try {
        const response = await axios.post(
          "/admin/getStudentInformation/createStudentInformation",
          data
        );
        if (response.code === 1) {
          this.$message.success("添加成功");
          this.FetchData();
        } else {
          this.$message.error("添加失败，该学号已存在");
        }
      } catch (error) {
        console.error("Axios error:", error);
      }
    },

    BasisInformationDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
    BasisInformationAddTableFormCloseButtonClick() {
      this.AddLineData();
      this.ifAddTable = false;
      this.RenewAddInput();
    },
    BasisInformationAddTableFormCloseNoButtonClick() {
      this.ifAddTable = false;
      this.RenewAddInput();
    },
  },
};
</script>

<style scoped>
.EditIcon {
  border: none;
}
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.StudentBasisPagination {
  position: absolute;
  top: 95%;
}
.BasisInformationUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.BasisInformationTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.BasisInformationTableBackground {
  position: absolute;
  width: 100%;
  height: 85%;
  top: 7%;
  overflow: auto;
}
.ControlBasisInformationTableButtonBackground {
  position: absolute;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/BasisInformationForm.css";
@import "/src/components/ManageSystem/AddTable/BasisInformationAddTable.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
